@use '../../styles/variables' as var;

.carousel {
  width: 100%;
  height: 40%;
  @media only screen and (max-width: var.$large-size-devices) {
    height: 30%;
  }
  @media only screen and (max-width: var.$small-size-devices) {
    height: 10rem;
  }

  .swiper {
    width: 100%;
    height: 100%;
    border-radius: 1rem;
    --swiper-navigation-size: 2.5rem;
    @media only screen and (max-width: var.$medium-size-devices) {
      --swiper-navigation-size: 1.75rem;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      --swiper-navigation-size: 1.25rem;
    }
    .logo {
      position: absolute;
      width: var.$banner-logo-width;
      z-index: 1000;
      right: calc(50% - 20%);
      top: 50%;
      margin-top: calc(0px - var.$banner-logo-width / 14);
    }
    .slide {
      width: 100%;
      height: 100%;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: top;
      }
      .gradient {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: 3;
        background: linear-gradient(
          to bottom,
          var.$carousel-gradient 60%,
          var.$black-1 100%
        );
      }
    }
  }
}
