@use '../../styles/variables' as var;

.speakerDetailWrapper {
  display: flex;
  padding: 0 20px;
  flex-direction: column;
  width: 22%;
  background-color: var(--theme-model-bg);
  overflow: scroll;
  position: fixed;
  right: -0px;
  margin-top: -2rem;
  animation: slideopen 1s;
  height: -webkit-fill-available;
  font-family: var.$primary-font-medium;
  color: var(--theme-color);
  z-index: 100;
  @media not all and (min-resolution: 0.001dpcm) {
    right: 2.5%;
    height: 88.5%;
  }
  @media only screen and (max-width: var.$large-size-devices) {
    width: 100%;
    @media not all and (min-resolution: 0.001dpcm) {
      width: 95%;
      height: 82%;
    }
  }
}
:focus {
  outline: none;
}

.speakerDetailWrapperClose {
  display: flex;
  flex-direction: column;
  width: 30%;
  background-color: var(--theme-model-bg);
  overflow: scroll;
  position: absolute;
  animation: slideclose 1s;
  right: -500px;
  top: 0px;
  height: -webkit-fill-available;
  font-family: var.$primary-font-medium;
  color: var(--theme-color);
}
:focus {
  outline: none;
}

.speakerDetaileHeader {
  padding: 0.8rem;
}

.speakerContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;

  img {
    width: 70%;
    height: 300px;
    border-radius: 10px;
    margin-bottom: 1rem;
    object-fit: cover;
    @media only screen and (max-width: var.$large-size-devices) {
      height: 600px;
    }
    @media only screen and (max-width: var.$medium-size-devices) {
      height: 600px;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      height: 300px;
    }
  }

  .speakerName {
    text-align: center;
    font-size: 1em;
    font-weight: bold;
    margin-bottom: 1.3rem;
    font-family: var.$primary-font-bold;

    @media only screen and (max-width: var.$large-size-devices) {
      font-size: 2em;
      margin-bottom: 2.6rem;
    }
    @media only screen and (max-width: var.$medium-size-devices) {
      font-size: 2em;
      margin-bottom: 2.6rem;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      font-size: 1em;
      margin-bottom: 1.3rem;
    }
  }

  .speakerDesc {
    font-family: var.$primary-font-medium;
    font-size: 0.8em;
    font-style: italic;
    @media only screen and (max-width: var.$large-size-devices) {
      font-size: 1.6em;
    }
    @media only screen and (max-width: var.$medium-size-devices) {
      font-size: 1.6em;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      font-size: 0.8em;
    }
  }
}

.eventWrapper {
  display: flex;
  flex-direction: column;
  width: 75%;
  align-items: center;
  margin-top: 0.8rem;
  .eventName {
    font-size: 1em;
    font-weight: bold;
    padding-bottom: 2px;
    border-bottom: 1px solid var(--theme-color);
    margin-bottom: 0.8rem;
    color: var(--theme-color);
    @media only screen and (max-width: var.$large-size-devices) {
      font-size: 2em;
      margin-top: 1.6rem;
    }
    @media only screen and (max-width: var.$medium-size-devices) {
      font-size: 2em;
      margin-top: 1.6rem;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      font-size: 1em;
      margin-top: 0.8rem;
    }
  }
  .eventDetailWrapper {
    display: flex;
    flex-direction: row;
    width: 100%;
    .eventIconWrapper {
      width: 17%;
      .eventIcon {
        margin-top: 5px;
        width: 22px;
        height: 27px;
        object-fit: none;
        fill: var(--theme-color);
      }
    }

    .eventDetailsWrapper {
      width: 83%;
      padding-left: 0.5rem;

      p {
        font-family: var.$primary-font-light;
        margin-bottom: 0.8rem;

        @media only screen and (max-width: var.$large-size-devices) {
          font-size: 1.6em;
          margin-bottom: 1.6rem;
        }
        @media only screen and (max-width: var.$medium-size-devices) {
          font-size: 1.6em;
          margin-bottom: 1.6rem;
        }
        @media only screen and (max-width: var.$small-size-devices) {
          font-size: 0.8em;
          margin-bottom: 0.8rem;
        }
      }
    }
  }
}

.speakerDetailFooter {
  margin-top: auto;
  padding-bottom: 1rem;
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
  align-items: center;

  :first-child {
    font-family: var.$primary-font-medium;
    font-size: 0.8em;
    font-style: italic;

    @media only screen and (max-width: var.$large-size-devices) {
      font-size: 1.6em;
    }
    @media only screen and (max-width: var.$medium-size-devices) {
      font-size: 1.6em;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      font-size: 0.8em;
    }
  }

  :last-child {
    cursor: pointer;
    font-family: var.$primary-font-light;
    font-style: normal;
    word-break: break-all;
  }
}

@keyframes slideopen {
  from {
    right: -500px;
  }
  to {
    right: 0px;
  }
}

@keyframes slideclose {
  from {
    right: 500px;
  }
  to {
    right: 0;
  }
}

.eventIcon {
  cursor: pointer;
  margin-top: 5px;
  width: 25px;
  height: 30px;
  object-fit: none;
  fill: var(--theme-color);
}

a,
a:link,
a:visited,
a:focus,
a:hover,
a:active {
  color: var(--theme-color);
  text-decoration: none;
}
