// Use same variable names in the light and dark classes and give their corresponding colors.

.light {
  --theme-color: #3c3a3a;
  --theme-page-background: #ffffff4f;
  --theme-agenda-background: #ffffff80;
  --theme-sessioninfo-background: #ffffff99;
  --theme-page-text: #111;
  --theme-toggle-button-color: #ffffffbd;
  --inactive-color: #717790c7;
  --button-inactive: #3c3a3a;
  --search-bg: #ffffff4f;
  --dropdown-bg: #f7f7f7;
  --overlay-bg: #ffffff4d;
  --dropdown-hover: #ececec00;
  --border-color: #ffffff59;
  --popup-bg: #ffffff00;
  --hover-menu-bg: #ffffff59;
  --scrollbar-bg: #fffdfd91;
  --content-title-color: --theme-color;
  --timer-color: #3c3a3a00;
  --theme-model-bg: #f9fafbf2;
  --hover-model-bg: #9997974d;
  --timer-color-stop: #f9fafb;
  --mobile-toggle-button-color: #ffffffbd;
  --theme-event-info-bg: #fff;
  --theme-session-break-bg1:#2727271a;
  --theme-session-break-bg2:#ffffff80;
  --theme-session-break-bg3:#ffffff99;
  --content-bg: #f9fafb;
}

.dark {
  --theme-color: #f9fafb;
  --theme-page-background: #10121b66;
  --theme-agenda-background: #10121b66;
  --theme-sessioninfo-background: #10121b66;
  --theme-page-text: #ead8d8;
  --theme-toggle-button-color: #00000069;
  --inactive-color: #717790c7;
  --hover-menu-bg: #0c0f194d;
  --content-title-color: #999ba5;
  --content-bg: #9297b321;
  --button-inactive: #f9fafb8c;
  --dropdown-bg: #21242d;
  --dropdown-hover: #2a2e3c00;
  --popup-bg: #16192500;
  --border-color: #71779040;
  --search-bg: #14162b;
  --overlay-bg: #24273b4d;
  --scrollbar-bg: #01020366;
  --timer-color: var(--theme-color);
  --theme-model-bg: #10121bf2;
  --hover-model-bg: #6b75984d;
  --timer-color: #f9fafb00;
  --timer-color-stop: #3c3a3a;
  --mobile-toggle-button-color: #6b75984d;
  --theme-event-info-bg: #10121bf2;
  --theme-session-break-bg1:#2727271a;
  --theme-session-break-bg2:#ffffff33;
  --theme-session-break-bg3:#10121b66;
}