@use "../../styles/remConverter" as r;
@use '../../styles/variables' as v;

.loader {
    width: r.rem(300px);
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: center;
    margin: 0 auto;
    
    @media only screen and (max-width: v.$small-size-desktop-devices) {
      width: r.rem(250px);
      }
    @media only screen and (max-width: v.$medium-size-devices) {
      width: r.rem(200px);
      }
    @media only screen and (max-width: v.$small-size-devices) {
      width: r.rem(150px);
    }
  }