@import "./variables";

/* ------------------------ font face : Starts ------------------------ */
@mixin fontFace($name) {
    @each $type in $font-types {
      @font-face {
        font-family: "#{$name}-#{$type}";
        src: local('#{$name}-#{$type}'),
            url("../assets/fonts/#{$name}/#{$name}-#{$type}.ttf") format('truetype');

        font-display: swap;
      }
    }
}
/* ------------------------ font face : Ends ------------------------ */