@use '../../styles/variables' as var;

.pageContainerWrapper {
  display: flex;
  position: relative;
  width: 80%;
  height: 100%;
  overflow: scroll;
  background-color: var(--theme-page-background);
  padding: 2rem 1.75rem;
  @media only screen and (max-width: var.$large-size-devices) {
    width: 100%;
    padding: 1rem;
  }
}
::-webkit-scrollbar 
{
  display:none;
}