@use '../../styles/variables' as var;

.leftNavContainer {
  width: 20%;
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0.8rem;
  @media only screen and (max-width: var.$small-size-desktop-devices) {
    width: 25%;
  }
  @media only screen and (max-width: var.$large-size-devices) {
    width: 100%;
    border: none;
    padding: 1.563rem;
    padding-bottom: 0.875rem;
  }
  .logoContainer {
    width: 70%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    @media only screen and (max-width: var.$large-size-devices) {
      width: 35%;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      width: 45%;
    }
  }
  .menu {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
  }
  .socialIcons {
    width: 40%;
    height: 1.6rem;
    margin-bottom: 30px;
    @media only screen and (max-width: var.$small-size-desktop-devices) {
      width: 50%;
      height: 1.3rem;
    }
    @media only screen and (max-width: var.$large-size-devices) {
      width: 23%;
      height: 1.75rem;
    }
    @media only screen and (max-width: var.$medium-size-devices) {
      width: 22%;
      height: 1.85rem;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      width: 40%;
      height: 1.65rem;
    }
  }
  .siriusLogo {
    width: 35%;
    cursor: pointer;
    img {
      width: 100%;
      aspect-ratio: 21/8.3;
    }
    @media only screen and (max-width: var.$small-size-desktop-devices) {
      width: 40%;
    }
    @media only screen and (max-width: var.$large-size-devices) {
      width: 15%;
    }
    @media only screen and (max-width: var.$medium-size-devices) {
      width: 14%;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      width: 30%;
    }
  }
}

.overlayContainer {
  position: absolute;
  top: 0;
  z-index: 6;
  background-color: var(--theme-model-bg);
  animation-name: FadeIn;
  animation-duration: 0.4s;
}

@keyframes FadeIn {
  from {opacity: 0}
  to {opacity: 1}
}