@use '../../styles/variables' as var;

.timers{
    display: flex;
    flex-wrap: nowrap;
    margin-left: 0.3rem;
    .colons{
        margin-left: 0.25rem;
        margin-right: 0.25rem;
        @media only screen and (max-width: var.$small-size-devices) {
          margin-left: 0.125rem;
          margin-right: 0.125rem;
        }
    }
    .numbers{
        width: 1.625rem;
        text-align: right;
        @media only screen and (max-width: var.$medium-size-devices) {
          width: 1.25rem;
        }
        @media only screen and (max-width: var.$small-size-devices) {
          width: 1rem;
        }
    }
}