@use './mixins';
@use './variables' as var;
@use './themes';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
  font-family: var.$primary-font-regular;
}
.swiper-button-prev,
.swiper-button-next {
  color: var.$swiper-button-color !important;
  @media only screen and (max-width: var.$small-size-devices) {
    width: 5px !important;
  }
}

// Styles for tooltip ( event info )
.tippy-arrow {
  color: var(--theme-event-info-bg) !important;
  width: 24px !important;
  height: 24px !important;

}

.tippy-box[data-placement^='top'] > .tippy-arrow:before {
  left: -4px !important;
  bottom: -12px !important;
  border-width: 12px 16px 0 !important;
}
.tippy-box[data-placement^='bottom'] > .tippy-arrow::before {
  left: -4px !important;
  top: -12px !important;
  border-width: 0 16px 12px !important;
  
}
.tippy-content {
  padding: 0 !important;
}
@include mixins.fontFace(var.$primary-font);

.imageCardsContainer {
  width: 100%;
  gap: var.$imageCardsGap;
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 2rem;
  @media only screen and (max-width: var.$large-size-devices) {
    padding-bottom: 1rem;
  }
  .imageWrapper {
    height: 20rem;
    width: calc((100% - 3 * (var.$imageCardsGap)) / 4);
    @media only screen and (max-width: var.$small-size-desktop-devices) {
      width: calc((100% - 2 * (var.$imageCardsGap)) / 3);
    //  height: 15rem;
    }
    @media only screen and (max-width: var.$large-size-devices) {
      width: calc((100% - 2 * (var.$imageCardsGap)) / 3);
      // height: 14rem;
    }
    @media only screen and (max-width: var.$medium-size-devices) {
      width: calc((100% - 1 * (var.$imageCardsGap)) / 2);
      height: 22rem;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      height: 25rem;
      width: 100%;
    }
  }
}