@use '../../styles/variables' as var;

.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2.5rem;
  height: 4rem;
  margin: 0 auto;
  border-bottom: 2px solid var(--border-color);
  @media only screen and (max-width: var.$large-size-devices) {
    padding: 0;
    border-bottom: none;
    height: max-content;
  }
  .menuCircle {
    width: 1.125rem;
    height: 1.125rem;
    background-color: var.$header-red;
    border-radius: 50%;
    box-shadow: 24px 0 0 0 var.$header-yellow, 48px 0 0 0 var.$header-green;
    @media only screen and (max-width: var.$large-size-devices) {
      display: none;
      width: 0.625rem;
      height: 0.625rem;
      box-shadow: 15px 0 0 0 var.$header-yellow, 30px 0 0 0 var.$header-green;
    }
  }
  .hamburgerContainer {
    position: absolute;
    top: 0.938rem;
    left: 0.625rem;
    z-index: 7;
  }
  .headerRight {
    display: flex;
    column-gap: 1.25rem;
    align-items: center;
    @media only screen and (max-width: var.$large-size-devices) {
      flex-direction: column;
      column-gap: 0.625rem;
      padding: 1.563rem;
    }
    .logoContainer {
      width: 70%;
      margin: 0 auto;
      @media only screen and (max-width: var.$large-size-devices) {
        width: 35%;
      }
      @media only screen and (max-width: var.$small-size-devices) {
        width: 45%;
      }
    }
    .timer {
      display: flex;
      background: var(--theme-color) -webkit-gradient(
          linear,
          left top,
          right top,
          from(var(--theme-color)),
          to(var(--theme-color)),
          color-stop(0.5, var(--timer-color-stop))
        ) 0 0 no-repeat;
      background-image: linear-gradient(
        -40deg,
        transparent 0%,
        transparent 40%,
        var(--timer-color-stop) 50%,
        transparent 60%,
        transparent 100%
      );
      font-size: 1.25rem;
      background-size: 12.5rem;
      color: var(--timer-color);
      background-clip: text;
      animation: shine 5s linear infinite;
      @media only screen and (max-width: var.$large-size-devices) {
        font-size: 1rem;
      }
      @media only screen and (max-width: var.$small-size-devices) {
        font-size: 0.75rem;
        margin: 0 auto;
      }
      @keyframes shine {
        0%,
        10% {
          background-position: -1000px;
        }
        100% {
          background-position: 1000px;
        }
      }
    }
  }
  .leftNavContainer {
    display: none;
    @media only screen and (max-width: var.$large-size-devices) {
      display: block;
    }
  }
}
