@use '../../styles/variables' as var;

.appWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .app {
    background-color: var(--theme-page-background);
    display: flex;
    height: 95vh;
    overflow: hidden;
    position: relative;
    width: 95%;
    border-radius: 14px;
    backdrop-filter: blur(20px);
    flex-direction: column;
    -webkit-backdrop-filter: blur(20px);
    flex-shrink: 0;
    .wrapper {
      height: 100%;
      display: flex;
      flex-grow: 1;
      overflow: hidden;
      height: 100%;
      @media only screen and (max-width: var.$large-size-devices) {
        display: block;
      }
    }
  }