// Colors
$white: #fff;
$black: #000;
$black-1:#111;
$dark-grey: #333333;
$color-gradient: linear-gradient(
  to right,
  #e9a680 0%,
  #cb4444 50%,
  #064cb5 100%
);
$stage-color : #80808026;
$stage-border-color : #808080b3;
$tech-talk-color :#cb444430;
$tech-talk-border-color : #cb444480;
$solution-space-color :#064cb530;
$solution-space-border-color : #064cb580;
$repeat-color : #e9a68030;
$repeat-border-color : #e9a680a4;
$carousel-gradient : #00000040;
$swiper-button-color: #ffffffbd;

// Theme Colors
$primary-theme-color: #f8f3f3;
$highlighted-theme-color: #efebeb;

// Text Colors
$grey-text: #3c3a3a;

//width
$toggle-mode-width: 3.5rem;
$toggle-mode-large-device-width: 4rem;
$light-mode-width: 1.5rem;
$light-mode-large-device-width: 2rem;
$dark-mode-width: 1.25rem;
$dark-mode-large-device-width: 1.75rem;
$speaker-image: 1.8rem;
$event-info-comtainer: 18.75rem;
$menu-component-width: 100%;
$nav-icon-width: 10%;
$banner-logo-width: 40%;

//height
$toggle-mode-height: 2rem;
$toggle-mode-large-device-height: 2.5rem;

//Device size breakpoints
$small-size-devices: 576px;
$medium-size-devices: 768px;
$large-size-devices: 992px;
$small-size-desktop-devices: 1200px;
$extra-extra-large-size-devices: 1400px;

//font
$font-types: ('Bold', 'Regular', 'Medium', 'Light', 'SemiBold');
$font-weights: (
  'regular': 400,
  'medium': 500,
  'semibold': 600,
  'bold': 700,
);
$primary-font: 'Poppins';
$primary-font-regular: 'Poppins-Regular';
$primary-font-light: 'Poppins-Light';
$primary-font-medium: 'Poppins-Medium';
$primary-font-bold: 'Poppins-Bold';
$primary-font-semibold: 'Poppins-SemiBold';

//calendar variable
$calendarFontColor: #525051;
$sample-calendar-background-color: #f4f1f3;

//Schedule Page Components Background Color
$componentBackgroundColor: #f4f1f3;

// Font Size
$hamburger-mobile-size: 1.25rem;
$menu-font-size: 1.5rem;
$hamburger-desktop-size: 1.875rem;

// Border Radius
$menu-list-item-border-radius: 0.625rem;

// Column Gap
$menu-list-items-col-gap: 1.875rem;

/// border radius variables
$border-radius-1: 5px;
$border-radius-2: 10px;
$border-color-black: #000;

// Text gradient on images
$image-text-gradient: linear-gradient(
  to bottom,
  #00000000 60%,
  #00000040 70%,
  #111 100%
);
//Gap
$imageCardsGap: 1rem;

// Background Animation Color 
$backGroundAnimation :linear-gradient(180deg, rgba(255, 255, 255, 0.72) 0%, rgba(255, 255, 255, 0.45) 100%);

//Image card Gradient
$black-shade-1 :#00000000;
$black-shade-2: #00000040;
$black-shade-3 :#111;

//Toggle light mode
$sun-color : #fdb813;

//Header three dots
$header-red:#f96057 ;
$header-yellow:#f8ce52 ;
$header-green:#5fcf65 ;

// AgendaDate 
$agenda-date:50px;
//Box Shadow
$box-shadow: -1px 3px 8px -1px rgba(0, 0, 0, 0.2);
$toggle-box-shadow :0 0 10px #00000033;
