@use '../../styles/variables' as var;

.toggleEffect {
  flex-wrap: nowrap;
  width: var.$toggle-mode-width;
  height: var.$toggle-mode-height;
  padding-top: 2px;
  background-color: var(--theme-toggle-button-color);
  border-radius: 38px;
  transform: scale(0.8);
  transform-origin: center center;
  cursor: pointer;
  outline: none;
  overflow: hidden;
  box-shadow: var.$toggle-box-shadow;
  @media only screen and (max-width: var.$large-size-devices) {
    width: var.$toggle-mode-large-device-width;
    height: var.$toggle-mode-large-device-height;
    background-color: var(--mobile-toggle-button-color);
  }
  @media only screen and (max-width: var.$small-size-devices) {
    width: var.$toggle-mode-width;
    height: var.$toggle-mode-height;
  }

  .lightMode {
    position: absolute;
    top: 4px;
    left: 28px;
    color: var.$sun-color;
    width: var.$light-mode-width;

    @media only screen and (max-width: var.$large-size-devices) {
      width: var.$light-mode-large-device-width;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      width: var.$light-mode-width;
    }
  }

  .darkMode {
    position: absolute;
    top: 6px;
    left: 8px;
    font-size: var.$dark-mode-width;

    @media only screen and (max-width: var.$large-size-devices) {
      font-size: var.$dark-mode-large-device-width;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      font-size: var.$dark-mode-width;
    }
  }

  @keyframes darkMode {
    0% {
      transform: translateX(-20px);
      scale: 0.8;
    }
    100% {
      transform: translateX(0px);
      scale: 1;
    }
  }
  @keyframes lightMode {
    0% {
      transform: translateX(20px);
      scale: 0.8;
    }
    100% {
      transform: translateX(0px);
      scale: 1;
    }
  }
}

#light,
#dark {
  .lightMode {
    animation-name: darkMode;
    animation-duration: 0.5s;
  }
  @keyframes lightMode {
    0% {
      transform: translateX(20px);
      scale: 0.8;
    }
    100% {
      transform: translateX(0px);
      scale: 1;
    }
  }
  .darkMode {
    animation-name: lightMode;
    animation-duration: 0.5s;
  }
  @keyframes darkMode {
    0% {
      transform: translateX(-30px);
      scale: 0.5;
    }
    100% {
      transform: translateX(0px);
      scale: 1;
    }
  }
}
