@use "../../styles/variables" as v;
.videoBg {
    position: fixed;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    
    video {
     width: 100%;
     height: 100%;
     object-fit: cover;
    }
}

.lightBg::before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height:100vh;
    background:v.$backGroundAnimation ;
    backdrop-filter: saturate(3);
  }