@use '../../styles/variables' as style;

.categoryCardContainer {
  font-family: style.$primary-font-light;
  border-radius: 1rem;
  box-shadow: style.$box-shadow;
  background-color: var(--content-bg);
  // @media only screen and (max-width: style.$large-size-devices) {
  //   border-radius: 0.5rem 0.5rem 1rem 1rem;
  // }
  .categoryImage {
    img {
      max-width: 100%;
      max-height: 100%;
      object-fit: cover;
      border-radius: 1rem 1rem 0 0;
      // @media only screen and (max-width: style.$large-size-devices) {
      //   border-radius: 0.5rem 0.5rem 0 0;
      // }
    }
  }
  .categoryContent {
    padding: 1.5rem 1.8rem;
    .categoryTitle {
      text-transform: uppercase;
      font-family: style.$primary-font-bold;
    }
    .categoryDescription {
      margin: 0.7rem 0;
    }
    .categoryDate {
      font-family: style.$primary-font-medium;
      font-style: italic;
    }
    .navButton {
      text-align: right;
      margin-top: 1rem;
      a {
        text-decoration: none;
        color: inherit;
      }
      span {
        border: 1px solid;
        border-radius: 20px;
        padding: 0.35rem 1.5rem;
      }
    }
  }
}
