@use '../../styles/variables' as var;
@use '../../styles/remConverter' as r;

.scannerContainer {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  padding: 3rem;
  justify-content: center;

  @media only screen and (max-width: var.$small-size-devices) {
    padding: 1rem;
  }
  .logo {
    width: 25%;
    margin: 3rem auto;
    display: flex;
    font-size: 1rem;
    align-items: center;
    @media only screen and (max-width: var.$large-size-devices) {
      width: 55%;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      width: 50%;
    }
  }
  .timer {
    display: flex;
    margin: 0 auto;
    background: var(--theme-color) -webkit-gradient(
        linear,
        left top,
        right top,
        from(var(--theme-color)),
        to(var(--theme-color)),
        color-stop(0.5, var(--timer-color-stop))
      ) 0 0 no-repeat;
    background-image: linear-gradient(
      -40deg,
      transparent 0%,
      transparent 40%,
      var(--timer-color-stop) 50%,
      transparent 60%,
      transparent 100%
    );
    font-size: 8rem;
    background-size: 12.5rem;
    color: var(--timer-color);
    background-clip: text;
    animation: shine 5s linear infinite;

    @media screen and (max-width: var.$extra-extra-large-size-devices) {
      font-size: 4.25rem;
    }
    @media only screen and (max-width: var.$large-size-devices) {
      font-size: 4rem;
    }
    @media only screen and (max-width: var.$medium-size-devices) {
      font-size: 2.5rem;
    }
    @media only screen and (max-width: var.$small-size-devices) {
      font-size: 1.75rem;
    }
    @keyframes shine {
      0%,
      10% {
        background-position: -3000px;
      }
      100% {
        background-position: 3000px;
      }
    }
    & div:nth-child(2) {
      margin: 0;
      & div:nth-child(3n-2) {
        width: 10rem;
        @media screen and (max-width: var.$extra-extra-large-size-devices) {
          width: 5.25rem;
        }
        @media only screen and (max-width: var.$medium-size-devices) {
          width: 3rem;
        }
        @media only screen and (max-width: var.$small-size-devices) {
          width: 2.125rem;
        }
      }
      & div:nth-child(3n) {
        margin-left: 0.5rem;
        margin-right: 0.5rem;
      }
    }
    & span:first-child {
      display: none;
    }
  }
}
