@use '../../styles/variables' as v;
@use '../../styles/remConverter' as r;
.imgCard {
  width: 100%;
  height: 100%;
  font-family: v.$primary-font-regular;
  color: v.$white;
  position: relative;
  .imgContainer {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: top;
      border-radius: v.$border-radius-2;
    }
    .imgTextContainer {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      padding: r.rem(15px) r.rem(20px);
      z-index: 5;
      @media only screen and (max-width: v.$small-size-devices) {
        padding: r.rem(12px);
      }
      .name {
        font-family: v.$primary-font-medium;
        font-size: r.rem(19.2px);
        text-transform: capitalize;
        // @media only screen and (max-width: v.$small-size-desktop-devices) {
        //   font-size: r.rem(14px);
        // }
      }
      .designation {
        font-size: r.rem(15px);
        font-family: v.$primary-font-light;
        // @media only screen and (max-width: v.$small-size-desktop-devices) {
        //   font-size: r.rem(12px);
        // }
      }
    }
  }
  .gradient {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 3;
    background: linear-gradient(
      to bottom,
      #00000000 50%,
      #00000040 60%,
      #111 100%
    );
    border-radius: r.rem(10px);
  }
}
