@use '../../styles/variables' as var;

.menuContainer {
  width: var.$menu-component-width;
  padding: 1.25rem 0;
  color: var(--theme-color);
  text-align: center;
  font-family: var.$primary-font-light;
  .navContents {
    list-style: none;
    a,
    a:link,
    a:visited,
    a:focus,
    a:hover,
    a:active {
      color: var(--theme-color);
      text-decoration: none;
    }
    .navIconContainer {
      width: var.$nav-icon-width;
      svg {
        height: 1.2rem;
        vertical-align: middle;
      }
      // svg {
      //   width: 1.875rem;
      //   height: 1.563rem;
      //   @media only screen and (max-width: var.$large-size-devices){
      //     width: 2.375rem;
      //     height: 1.875rem;
      //   }
      //   @media only screen and (max-width: var.$small-size-devices){
      //     width: 1.875rem;
      //     height: 1.563rem;
      //   }
      // }
    }
    li {
      display: flex;
      justify-content: left;
      align-items: center;
      column-gap: var.$menu-list-items-col-gap;
      border-radius: var.$menu-list-item-border-radius;
      padding: 0.75rem 0 0.75rem 1.8rem;
      margin-bottom: 0.156rem;
      cursor: pointer;
      h5 {
        margin-top: 0.313rem;
        font-size: 1.2rem;
      }
      div:last-child {
        align-self: center;
        margin-left: auto;
      }
    }
    li:hover {
        background-color: var(--hover-menu-bg);
    }
    .themeToggle {
      display: none;
      @media only screen and (max-width: var.$large-size-devices){
          display: flex;
      }
    }
    .themeToggle:hover {
      background-color: transparent;
    }
  }
}

// Styling for Active Nav
.activeNav {
  > li {
    background-color:  var(--hover-menu-bg);
  }
}

// Styling for Active Nav in Mobile screens
.overlayNav {
  > li {
    background-color: var(--hover-model-bg);
  }
}