@use '../../styles/variables' as v;

body.isActive {
  .heroLogo {
    top: 70px;
    left: 127px;
    width: 13.5rem;
    margin-top: 0;
    margin-left: -40px;
    svg {
      width: 100%;
      height: auto;
      opacity: 1;
    }
    @media only screen and (max-width: v.$small-size-desktop-devices) {
      top: 11%;
      left: 10%;
      width: 9rem;
    }
    @media only screen and (max-width: v.$medium-size-devices) {
      left: 40%;
      top: 4%;
      width: 16rem;
    }
    @media only screen and (max-width: v.$small-size-devices) {
      left: 46%;
      top: 4%;
      width: 7.2rem;
    }
  }
}

.heroLogo {
  display: block;
  width: 300px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -200px;
  margin-top: -75px;
  transition: all 1000ms linear;
  @media only screen and (max-width: v.$small-size-desktop-devices) {
    top: 55%;
    left: 56%;
  }
  @media only screen and (max-width: v.$medium-size-devices) {
    top: 56%;
    left: 56%;
  }
  @media only screen and (max-width: v.$small-size-devices) {
    top: 57%;
    left: 69%;
  }
  svg {
    height: 140px;
    width: 500px;
    transition: all 1000ms linear;
    stroke: url(#linear-gradient);
    stroke-width: 3;
    stroke-miterlimit: 10;
    fill: url(#linear-gradient);
    opacity: 1;
    @media only screen and (max-width: v.$small-size-desktop-devices) {
      height: 100px;
      width: 350px;
    }
    @media only screen and (max-width: v.$medium-size-devices) {
      height: 100px;
      width: 350px;
    }
    @media only screen and (max-width: v.$small-size-devices) {
      height: 50px;
      width: 270px;
    }
  }
  &.isAnimated {
    svg {
      opacity: 1;
      z-index: 10;
    }
  }
  .heroLogoPath {
    fill: transparent;
    stroke-dasharray: 0;
    animation: stroke-anime 12s linear forwards;
  }
  @keyframes stroke-anime {
    from {
      stroke-dashoffset: 0;
      stroke-dasharray: 100;
    }
    to {
      stroke-dashoffset: 0;
      stroke-dasharray: 3000;
    }
  }
  &.endAnimation {
    .heroLogoPath {
      fill: url(#linear-gradient);
      fill-opacity: 0;
      animation: fill-anim 1s ease forwards 0.3s;
      animation-fill-mode: forwards;
    }
  }
  @keyframes fill-anim {
    0% {
      fill-opacity: 0;
    }
    50% {
      fill-opacity: 0.5;
    }
    100% {
      fill-opacity: 1;
    }
  }
}
