@use '../../styles/variables' as style;

.homePageWrapper {
  width: 100%;
  height: 100%;
  scroll-margin: 2rem;
  .categoryCardWrapper {
    display: flex;
    column-gap: 2rem;
    margin-top: 1.5rem;
    margin-bottom: 0.25rem;
    border: 1px solid var(--theme-bg-color);
    padding: 0 0.25rem;
    padding-bottom: 2rem;
    @media only screen and (max-width: style.$large-size-devices) {
      padding-bottom: 1rem;
    }
    @media only screen and (max-width: style.$medium-size-devices) {
      flex-direction: column;
      row-gap: 1rem;
      width: 100%;
    }
  }
}
